import React, { useState } from 'react';
import { Link, Route, Routes, useLocation, useNavigate } from 'react-router-dom';
import './App.css';
import Homepage from './Homepage';
import About from './About';
import Contact from './Contact';
import Assignment from './Assignment';
import Register from './Register';
import Login from './Login';
import Courses from './Courses';
import Performance from './Performance';
import CourseContent from './CourseContent';
import WebDevCourse from './courses/WebDevCourse';
import ReactJSCourse from './courses/ReactJSCourse';
import ExpressJSCourse from './courses/ExpressJSCourse';
import DjangoCourse from './courses/DjangoCourse';
import JavaCourse from './courses/JavaCourse';
import PythonCourse from './courses/PythonCourse';
import AWSCourse from './courses/AWSCourse';
import AICourse from './courses/AICourse';
import GenAICourse from './courses/GenAICourse';
import DevOpsCourse from './courses/DevOpsCourse';

const App = () => {
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const location = useLocation();
  const navigate = useNavigate();

  // Helper function for protected navigation
  const navigateIfLoggedIn = (path) => {
    if (localStorage.getItem("Login") === "yes") {
      navigate(path);
    } else {
      alert("Please Login to access this page.");
      navigate("/login"); // Or navigate to home: navigate("/react")
    }
  };

  // Function to check if a link is active
  const isActive = (path) => {
    return location.pathname === path ? 'active' : '';
  };

  // Handle search input
  const handleSearch = (e) => {
    setSearchQuery(e.target.value);
    // You can implement search functionality here
  };

  // Handle logout
  const handleLogout = () => {
    setIsLoggedIn(false);
    localStorage.setItem("Login","no")
    navigate('/react');
  };

  // Function to handle successful login
  const handleLoginSuccess = () => {
    setIsLoggedIn(true);
  };

  return (
    <div>
      <nav id='Nav_parent'>
        <Link to='/react' className={`nav-button ${isActive('/')}`}>
          Home
        </Link>
        <Link 
          to='/About' 
          onClick={(e) => {
            e.preventDefault();
            navigateIfLoggedIn('/About');
          }}
          className={`nav-button ${isActive('/About')}`}
        >
          About
        </Link>
        <div className="search-container">
          <input 
            id='srch' 
            placeholder='Search...' 
            type='search'
            value={searchQuery}
            onChange={handleSearch}
            aria-label="Search"
          />
        </div>
        <Link 
          to='/Contact'
          onClick={(e) => {
            e.preventDefault();
            navigateIfLoggedIn('/Contact');
          }}
          className={`nav-button ${isActive('/Contact')}`}
        >
          Contact Us
        </Link>
        <Link 
          to='/Courses'
          onClick={(e) => {
            e.preventDefault();
            navigateIfLoggedIn('/CourseContent');
          }}
          className={`nav-button ${isActive('/Courses')}`}
        >
          Courses
        </Link>
        <Link 
          to='/Assignments'
          onClick={(e) => {
            e.preventDefault();
            navigateIfLoggedIn('/Assignments');
          }}
          className={`nav-button ${isActive('/Assignments')}`}
        >
          Assignments
        </Link>
        <Link 
          to='/Performance'
          onClick={(e) => {
            e.preventDefault();
            navigateIfLoggedIn('/Performance');
          }}
          className={`nav-button ${isActive('/Performance')}`}
        >
          Performance
        </Link>

        {isLoggedIn ? (
          <button 
            onClick={handleLogout}
            className="logout-button"
          >
            Logout
          </button>
        ) : (
          <div className="auth-links">
            <button 
              className={`nav-button ${isActive('/login')}`}
              onClick={() => navigate('/login')}
            >
              Login
            </button>
            <span>/</span>
            <button 
              className={`nav-button ${isActive('/signup')}`}
              onClick={() => navigate('/register')}
            >
              Signup
            </button>
          </div>
        )}
      </nav>

      <main id='bgc' className="main-content">
        <Routes>
          <Route path='/react' element={<Homepage />} />
          <Route path='/About' element={<About />} />
          <Route path='/Contact' element={<Contact />} />
          <Route path='/Assignments' element={<Assignment />} /> {/* Corrected path */}
          <Route path='/login' element={<Login onLoginSuccess={handleLoginSuccess} />} />
          <Route path='/register' element={<Register />} />
          <Route path='/CourseContent' element={<CourseContent />} />
          <Route path='/Performance' element={<Performance />} />
          <Route path='/CourseContent' element={<CourseContent />} />
          <Route path='/webdev' element={<WebDevCourse />} />
          <Route path='/reactjs' element={<ReactJSCourse />} />
          <Route path='/expressjs' element={<ExpressJSCourse />} />
          <Route path='/django' element={<DjangoCourse />} />
          <Route path='/java' element={<JavaCourse />} />
          <Route path='/python' element={<PythonCourse />} />
          <Route path='/aws' element={<AWSCourse />} />
          <Route path='/ai' element={<AICourse />} />
          <Route path='/genai' element={<GenAICourse />} />
          <Route path='/devops' element={<DevOpsCourse />} />
        </Routes>
      </main>
    </div>
  );
};

export default App;
