import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './Courses.css';

const courseList = [
  {
    id: 'webdev',
    title: 'Web Development',
    description: 'Learn HTML, CSS, JavaScript, and modern web development practices',
    duration: '12 weeks',
    icon: ''
  },
  {
    id: 'reactjs',
    title: 'ReactJS',
    description: 'Master React.js including hooks, context, and state management',
    duration: '8 weeks',
    icon: ''
  },
  {
    id: 'expressjs',
    title: 'ExpressJS',
    description: 'Build robust backend services with Express.js and Node.js',
    duration: '6 weeks',
    icon: ''
  },
  {
    id: 'django',
    title: 'Django',
    description: 'Create powerful web applications with Python and Django',
    duration: '10 weeks',
    icon: ''
  },
  {
    id: 'java',
    title: 'Java',
    description: 'Learn core Java programming and enterprise development',
    duration: '14 weeks',
    icon: ''
  },
  {
    id: 'python',
    title: 'Python',
    description: 'Master Python programming from basics to advanced concepts',
    duration: '10 weeks',
    icon: ''
  },
  {
    id: 'aws',
    title: 'AWS',
    description: 'Cloud computing and deployment with Amazon Web Services',
    duration: '8 weeks',
    icon: ''
  },
  {
    id: 'ai',
    title: 'AI',
    description: 'Artificial Intelligence fundamentals and applications',
    duration: '12 weeks',
    icon: ''
  },
  {
    id: 'genai',
    title: 'Generative AI',
    description: 'Learn about LLMs, diffusion models, and AI content generation',
    duration: '8 weeks',
    icon: ''
  },
  {
    id: 'devops',
    title: 'DevOps',
    description: 'Master CI/CD, containerization, and deployment automation',
    duration: '10 weeks',
    icon: ''
  },
];

const Homepage = () => {
  const [enrolledCourses, setEnrolledCourses] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const username = localStorage.getItem('name') || 'guest';
  const navigate = useNavigate();

  useEffect(() => {
    try {
      setLoading(true);
      const storedData = localStorage.getItem(`enrolled_${username}`);
      if (storedData) {
        const parsedData = JSON.parse(storedData);
        setEnrolledCourses(parsedData);
      }
      setLoading(false);
    } catch (err) {
      setError('Failed to load enrolled courses. Please try again.');
      setLoading(false);
    }
  }, [username]);

  const enroll = (courseId) => {
    try {
      if (!username || username === 'guest') {
        alert('Please login to enroll in courses');
        return;
      }

      if (enrolledCourses[courseId]) {
        alert(`You are already enrolled in ${courseList.find(c => c.id === courseId).title}`);
        return;
      }

      const course = courseList.find(c => c.id === courseId);
      const updated = {
        ...enrolledCourses,
        [courseId]: {
          status: 'Enrolled',
          progress: 0,
          enrolledDate: new Date().toISOString(),
          lastUpdated: new Date().toISOString(),
          quizScores: []
        }
      };
      setEnrolledCourses(updated);
      localStorage.setItem(`enrolled_${username}`, JSON.stringify(updated));
      
      // Show success message and redirect
      alert(`${username} Successfully enrolled in ${course.title}`);
      navigate('/course-content');
    } catch (err) {
      setError('Failed to enroll in the course. Please try again.');
    }
  };

  const getStatus = (courseId) => {
    return enrolledCourses[courseId]?.status || 'Not Enrolled';
  };

  return (
    <div className="courses-container">
      <h2>Available Courses</h2>
      {error && <div className="error-message">{error}</div>}
      {loading ? (
        <div className="loading">Loading courses...</div>
      ) : (
        <div className="courses-grid">
          {courseList.map(course => (
            <div key={course.id} className="course-card">
              <div className="course-icon">{course.icon}</div>
              <h3>{course.title}</h3>
              <p className="course-description">{course.description}</p>
              <p className="course-duration">Duration: {course.duration}</p>
              
              <p className={`status ${getStatus(course.id).toLowerCase().replace(' ', '-')}`}>
                {getStatus(course.id)}
              </p>

              {getStatus(course.id) === 'Not Enrolled' && (
                <button 
                  className="enroll-button"
                  onClick={() => enroll(course.id)}
                >
                  Enroll Now
                </button>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Homepage;
