import React, { useState, useEffect } from 'react';
import { Line, Pie, Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  ArcElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  ArcElement,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const Performance = () => {
  const [performanceData, setPerformanceData] = useState(null);
  const username = localStorage.getItem('name') || 'guest';

  useEffect(() => {
    if (username && username !== 'guest') {
      const storedData = localStorage.getItem(`enrolled_${username}`);
      if (storedData) {
        const coursesData = JSON.parse(storedData);
        processPerformanceData(coursesData);
      }
    }
  }, [username]);

  const processPerformanceData = (coursesData) => {
    const courseProgress = Object.entries(coursesData).map(([id, data]) => ({
      id,
      progress: data.progress || 0,
      status: data.status,
      quizScores: data.quizScores || [],
      lastUpdated: data.lastUpdated
    }));

    const completedCourses = courseProgress.filter(c => c.status === 'Completed').length;
    const inProgressCourses = courseProgress.filter(c => c.status === 'In Progress').length;
    const totalCourses = courseProgress.length;

    const averageProgress = courseProgress.reduce((acc, curr) => acc + curr.progress, 0) / totalCourses;

    const quizPerformance = courseProgress.reduce((acc, curr) => {
      if (curr.quizScores && curr.quizScores.length > 0) {
        acc[curr.id] = curr.quizScores.reduce((sum, score) => sum + score, 0) / curr.quizScores.length;
      }
      return acc;
    }, {});

    setPerformanceData({
      courseProgress,
      completedCourses,
      inProgressCourses,
      averageProgress,
      quizPerformance
    });
  };

  const pieChartData = {
    labels: ['Completed', 'In Progress', 'Not Started'],
    datasets: [
      {
        data: performanceData ? [
          performanceData.completedCourses,
          performanceData.inProgressCourses,
          Object.keys(performanceData.quizPerformance).length - 
            (performanceData.completedCourses + performanceData.inProgressCourses)
        ] : [0, 0, 0],
        backgroundColor: ['#4CAF50', '#2196F3', '#FFC107'],
      },
    ],
  };

  const lineChartData = {
    labels: performanceData?.courseProgress.map(c => c.id) || [],
    datasets: [
      {
        label: 'Course Progress',
        data: performanceData?.courseProgress.map(c => c.progress) || [],
        borderColor: '#2196F3',
        tension: 0.1,
      },
    ],
  };

  const barChartData = {
    labels: Object.keys(performanceData?.quizPerformance || {}),
    datasets: [
      {
        label: 'Quiz Performance',
        data: Object.values(performanceData?.quizPerformance || {}),
        backgroundColor: '#4CAF50',
      },
    ],
  };

  const chartOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Your Learning Progress',
      },
    },
    scales: {
      y: {
        beginAtZero: true,
        max: 100,
      },
    },
  };

  return (
    <div className="performance-container">
      <h2>Performance Dashboard</h2>
      {username === 'guest' ? (
        <p>Please log in to view your performance metrics.</p>
      ) : !performanceData ? (
        <p>No course data available. Enroll in courses to track your progress.</p>
      ) : (
        <div className="charts-grid">
          <div className="chart-card">
            <h3>Course Status Overview</h3>
            <div className="chart-wrapper">
              <Pie data={pieChartData} options={{ ...chartOptions, aspectRatio: 1 }} />
            </div>
          </div>
          <div className="chart-card">
            <h3>Progress Across Courses</h3>
            <div className="chart-wrapper">
              <Line data={lineChartData} options={chartOptions} />
            </div>
          </div>
          <div className="chart-card">
            <h3>Quiz Performance</h3>
            <div className="chart-wrapper">
              <Bar data={barChartData} options={chartOptions} />
            </div>
          </div>
          <div className="stats-card">
            <h3>Quick Stats</h3>
            <div className="stats-grid">
              <div className="stat-item">
                <h4>Average Progress</h4>
                <p>{performanceData.averageProgress.toFixed(1)}%</p>
              </div>
              <div className="stat-item">
                <h4>Completed Courses</h4>
                <p>{performanceData.completedCourses}</p>
              </div>
              <div className="stat-item">
                <h4>Active Courses</h4>
                <p>{performanceData.inProgressCourses}</p>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Performance;
