import React from 'react';
import './App.css'; // Import the CSS file

const assignments = [
  {
    id: 1,
    title: 'Introduction to Web Development: HTML & CSS Basics',
    description: 'Create a personal portfolio website using HTML and CSS. Focus on semantic HTML and responsive design principles.',
    dueDate: '2024-08-15',
    status: 'Pending',
  },
  {
    id: 2,
    title: 'JavaScript Fundamentals: Interactive Quiz App',
    description: 'Develop an interactive quiz application using vanilla JavaScript. Implement features like multiple-choice questions, scoring, and immediate feedback.',
    dueDate: '2024-08-30',
    status: 'Pending',
  },
  {
    id: 3,
    title: 'React.js: Building a To-Do List Application',
    description: 'Build a dynamic to-do list application using React. Utilize components, state management, and props to create a functional user interface.',
    dueDate: '2024-09-15',
    status: 'In Progress',
  },
  {
    id: 4,
    title: 'Node.js & Express: REST API Development',
    description: 'Design and implement a RESTful API for a blog platform using Node.js and Express. Include CRUD operations for posts and comments.',
    dueDate: '2024-09-30',
    status: 'Not Started',
  }
];

const Assignment = () => {
  return (
    <div className="page-section">
      <h2>Your Assignments</h2>
      {assignments.length > 0 ? (
        <ul className="assignment-list">
          {assignments.map(assignment => (
            <li key={assignment.id}>
              <h3>{assignment.title}</h3>
              <p><strong>Description:</strong> {assignment.description}</p>
              <p>
                <strong>Due Date:</strong> {assignment.dueDate} |
                <strong> Status: </strong>
                <span className={`status-${assignment.status.toLowerCase().replace(/\s+/g, '-')}`}>
                  {assignment.status}
                </span>
              </p>
            </li>
          ))}
        </ul>
      ) : (
        <p>You have no assignments at the moment. Check back later!</p>
      )}
    </div>
  );
};

export default Assignment;